.share{
    width: 100%;
    height: 170px;
    border-radius: 10px;
    -webkit-box-shadow: -2px 2px 27px 22px rgba(0,0,0,0.14);
    -moz-box-shadow: -2px 2px 27px 22px rgba(0,0,0,0.14);
    box-shadow: -2px 2px 27px 22px rgba(0,0,0,0.14);
    /*https://www.cssmatic.com/box-shadow use this site*/
}
.shareWrapper{
    padding: 10px;
}
.shareTop{
    display: flex;
    align-items: center;
}
.shareProfileImg{
    width: 50px;
    height: 50px;
    border-radius: 50%;
    object-fit: cover;
    margin-right: 10px;
}
.shareInput{
    border: none;
    width: 80%;
}
.shareInput:focus{
    outline: none;
}
.shareHr{
    margin: 20px;
}
.shareBottom{
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.shareOptions{
    display: flex;
    margin-left: 20px;
}
.shareOption{
    display: flex;
    align-items: center;
    margin-right: 15px;
    cursor: pointer;
}
.shareIcon{
    font-size: 18px;
    margin-right: 3px;
}
.shareOptionText{
    font-size: 14px;
    font-weight: 500;
}
.shareButton{
    padding: 7px;
    border-radius: 5px;
    background-color: rgb(223, 219, 219);
    border-color: rgb(223, 219, 219);
    background-color: #FF6000;
    font-weight: 500;
    margin-right: 20px;
    cursor: pointer;
    color: white;
}